import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ProductsProvider  } from '../../providers/products/products';
import { CartProvider } from '../../providers/cart/cart';

@IonicPage()
@Component({
  selector: 'page-my-bag',
  templateUrl: 'my-bag.html',
})
export class MyBagPage {
  public ProductList:any;
  public couponDetail:boolean;
  public getCouponData:boolean;
  public applyCoupon:boolean;
  public discountDetail:boolean;
  private cartList:any = [];

  constructor(public navCtrl: NavController, public navParams: NavParams,
    private productsProvider:ProductsProvider,
  public cartProvider:CartProvider) {
    this.couponDetail = true;
    this.getCouponData = true;
    this.applyCoupon = false;
    this.discountDetail = false;
  }

  ionViewDidLoad() {
      this.ProductList = 'MyWishList';  
  }

  getCouponCode(){
    this.getCouponData = false;
    this.applyCoupon = true;
  }

  applyCouponCode(){
    this.couponDetail = false;
    this.discountDetail = true;

  }


  // ionViewWillLoad(){
  //   this.productsProvider.getCartItems(localStorage.getItem('useremail'),localStorage.getItem('token')).subscribe(cartItems=>{
  //     console.log( cartItems,"got response from cart");
  //        this.cartList = cartItems.Carts;
  //       console.log( this.cartList,"got response from cart");
  //   })
  // }

  ionViewWillLoad(){
    this.productsProvider.getCartItems(localStorage.getItem('useremail'),localStorage.getItem('token')).subscribe(cartItems=>{
      console.log( cartItems.Carts.CartItems[0].Quantity,"::::::::got quantity:::::::::");
      this.cartProvider.setCartItemQuantity(cartItems.Carts.CartItems[0].Quantity);
         this.cartList = cartItems.Carts;
        console.log( this.cartList,"got response from cart");
    })
  }
}



